@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,300,300italic,400italic,600,600italic,700,700italic,800,800italic|Roboto+Condensed:400,300,300italic,400italic,700,700italic|Patua+One|Permanent+Marker);

$default-font: Open Sans, sans-serif;
$condensed-font: Roboto Condensed, sans-serif;
$logo-font: Patua One, serif;
$handwritten-font: Permanent Marker, sans-serif;

$font-size-in-pixels: 18px;

html,
body {
  font-family: $default-font;
  font-size:   $font-size-in-pixels;
}

h1 {
  font-size:  1.6em;
}

h2 {
  font-size:  1.4em;
}

h3 {
  font-size:  1.2em;
}

h4,
h5,
h6 {
  font-size:  1em;
}

#header {
  a {
    text-decoration: none;
  }
}
